import * as React from 'react';

import * as styles from '../../less/input.module.less';

/**
 * @param type input 
 * @param maxLength 
 * @param name name
 * @param className 
 * @param style 
 * @param placeholder 
 * @param error form
 * @param [key: string]  
 */

interface Props {
    type?: string | undefined;
    maxLength?: number | undefined;
    name: string;
    className?: string | undefined;
    style?: React.CSSProperties | undefined;
    placeholder?: string;
    error?: string;
    [key: string]: any;
}

interface InputProps extends Props {
    onInput?: ((value: string) => void) | undefined;
    onChange?: ((value: string) => void) | undefined;
}

export default class Input extends React.Component<InputProps> {
    constructor(props: InputProps) {
        super(props)
    }
    public onChange = (event: React.FormEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        let value = event.currentTarget.value;
        this.props.onChange && this.props.onChange(value);
    }
    public onInput = (event: React.FormEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        let value = event.currentTarget.value;
        this.props.onInput && this.props.onInput(value);
    }
    public render() {
        let type = {
            ...this.props,
            type: this.props.type,
            maxLength: this.props.maxLength || undefined,
            name: this.props.name,
            className: styles.input,
            style: {},
            onInput: this.onInput,
            onChange: this.onChange,
            placeholder: this.props.placeholder,
            'data-error': this.props.error,
        }
        if (this.props.type === 'mobile') {
            type.type = 'tel';
            type.maxLength = 11;
            type.placeholder = type.placeholder || '';
            type['data-type'] = 'mobile';
        }
        return (
            <div style={ this.props.style } className={ this.props.className }>
                {
                    this.props.type === 'textarea' ?
                    <textarea {...type} /> :
                    <input {...type} />
                }
            </div>
        )
    }
}